          this.headbgType = value.backgroundType
          if (value.backgroundType === 2) { // 主题设置为背景色值
            let arr = value.background && value.background.split(',')
            if (arr.length > 1) {
              this.headerBg = arr[0] + ` url(${arr[1]}) center top / 100% no-repeat`;
