<template>
  <div class="find-goods" :class="{'goods-next': next}" @click="goPage">
    <div class="goods-title">
      <h4>发现好货</h4>
      <template v-if="info.type=='product'">
        <div class="goods-part" v-if="!next"><span>{{info.timeStr && info.timeStr.split(':')[0]}}点场</span>{{countTime}}</div>
        <div class="goods-part" v-else-if="info.timeStr"><span>{{info.timeStr}}</span>活动预告</div>
      </template>
    </div>
    <div class="goods-desc">精心挑选每日上新</div>
    <div class="goods-product" v-if="info.products">
      <div v-for="item in info.products" :key="item.id">
        <div class="goods-pro-img">
          <vanImg :src="item.imageUrl | imageToHttps | imageToWebp($webpSurpport)" alt="" />
        </div>
        <p>￥<span class="price-num">{{item.lowestPrice || item.flashsalePrice}}</span><span v-if="item.lowestPrice" class="lowest-tip">起</span></p>
        <del>￥<span class="price-num">{{item.lowestCrossedPrice || item.miniPrice}}</span><span v-if="item.lowestCrossedPrice" class="lowest-tip">起</span></del>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'findGoods',
  props: {
    count: {
      type: Number,
      default: () => 0
    },
    info: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      next: false, // 是否是预告
      hour: '',
      min: '',
      sec: '',
      timer: null, // 定时器
      remain: 0, // 剩余时间
    }
  },
  computed: {
    countTime() {
      return this.hour + ':' + this.min + ':' + this.sec;
    }
  },
  watch: {
    count: {
      handler(newVal, oldVal) {
        if (newVal > 0 && oldVal !== newVal) {
          this.remain = this.count
          this.countdown()
          this.next = false
        } else {
          this.next = true
        }
      },
      immediate: true
    }
  },
  methods: {
    countdown() {
      clearInterval(this.timer)
      this.timer = setInterval(this.computeHourMinSec, 1000);
      this.computeHourMinSec()
    },
    computeHourMinSec() {
      let count = this.remain;
      if (count <= 0) {
        clearInterval(this.timer);
        this.$emit('end')
        return;
      }
      count--;
      this.remain = count
      let h = Math.floor(count / 3600);
      let m = Math.floor((count % 3600) / 60);
      let s = count % 60;
      this.hour = this.toDoubleNumber(h);
      this.min = this.toDoubleNumber(m);
      this.sec = this.toDoubleNumber(s);
    },
    toDoubleNumber(num) {
      return num < 10 ? '0' + num : '' + num;
    },
    goPage() {
      // 埋点
      this.$util.gioTrackWithVariable('MB15598', {
        text: '发现好货'
      })
      if (this.info.type === 'product') {
        this.$router.push(`/flashSale`)
      } else if (this.info.type === 'store') {
        this.$router.push(`/flashSaleList`)
      }
    }
  }
}
</script>

