<template>

  <div class="store-warp" v-if="type==1 && storelength">
    <div class="store-list">
       <div class="store-item" :class="{'store-block': mstore.length == 1}" v-for="item in mstore" :key="item.storeId" @click="goNearby(item)">
         <div class="store-img">
           <img v-if="item.storeImg" :src="item.storeImg | imageToHttps | imageToWebp($webpSurpport)" alt="">
           <img v-else :src="getDefaultImageUrl(item)" alt="">
         </div>
         <div class="store-body">
            <h4>{{item.storeAliasName}}</h4>
            <div><span class="icon-shop"></span><span class="icon-guanfang"></span></div>
         </div>
      </div>
      <div class="store-more" v-if="storelength > 5" @click="more()">
        <i></i>
        <p>查看更多</p>
      </div>
    </div>
  </div>

  <div class="nearby-shop" v-else-if="type==0">
    <div class="nearby-title">
      <h4>附近好店</h4>
      <a href="javascript:;" @click="look">查看 <i></i></a>
    </div>
    <div class="shop-title" @click="look">
      <h4><p>{{store.storeName}}</p> <!--<span>官方认证</span>--></h4>
      <p>距您{{store.distance | fixedDistance}}{{store.distance > 1000 ? 'km' : 'm'}}</p>
    </div>
    <div class="shop-product" @click="look">
      <img v-for="(img, index) in productImgUrl" :key="index" :src="img.defaultImageUrl">
    </div>
  </div>
</template>

<script>

export default {
  name: 'nearbyShop',
  props: {
    store: {
      type: Object,
      default: () => {}
    },
    mstore: {
      type: Array,
      default: () => []
    },
    storelength: {
      type: Number,
      default: () => 0
    },
    type: {
      type: Number,
      default: () => 0
    },
    productImgUrl: {
      type: Array,
      default: () => []
    }
  },
  filters: {
    // 处理距离
    fixedDistance(value) {
      try {
        if (value >= 1000) {
          value = value / 1000
          return value.toFixed(1)
        } else {
          return value
        }
      } catch {
        return 0
      }
    },
  },
  methods: {
    getDefaultImageUrl(store) {
      if (this.$util.isSyBird() || Number(store.synStoreType) !== 9) {
        return `${this.$cdnImageUrl}/sybird_default_logo.png`
      } else {
        return `${this.$cdnImageUrl}/zhijia.png`
      }
    },
    more() {
      this.$router.push('/vipstore')
    },
    look() {
      // 埋点
      this.$util.gioTrackWithVariable('MB15597', {
        storeId: this.store.storeId ? this.store.storeId + '' : 'null'
      })
      this.$router.push(`/shopDetail?storeId=${this.store.storeId}`)
    },
    goNearby(item) {
      // 埋点
      this.$util.gioTrackWithVariable('MB15596', {
        storeId: item.storeId ? item.storeId + '' : 'null'
      })
      this.$router.push(`/shopDetail?storeId=${item.storeId}`)
    }
  }
}
</script>

