<template>
  <div class="activitys-dialog-warp">
    <div
      class="dialog-mask"
      ref="dialogMask"
      @touchmove.stop.prevent
      v-if="dialogInfoList.length > 0"
    >
      <template>
        <van-swipe
          :loop="false"
          class="coupon-swipe"
          :show-indicators="dialogInfoList.length > 1"
          indicator-color="white"
        >
          <van-swipe-item
            class="coupon-swiper-item"
            :key="index"
            v-for="(item, index) in dialogInfoList"
          >
            <div
              class="dialog"
              :style="dialogStyle"
              ref="dialog"
            >
              <div class="dialog-warp">
                <div
                  class="dialog-body"
                  @touchmove.stop.prevent
                >
                  <div
                    class="dialog-close"
                    ref="closeIcon"
                    @click="dialogCloseFn(index)"
                  ></div>
                  <span class="icon-tag"></span>
                  <div
                    class="dialog-quan"
                    v-if="dialogInfoList[index].dialogStatus==1"
                  >
                    <div
                      class="dialog-quan-list"
                      @touchmove.stop="touchHandler($event, index)"
                    >
                      <div
                        class="dialog-quan-item"
                        v-for="(quan, index) in dialogInfoList[index].coupons"
                        :key="index"
                      >
                        <div class="item-fl">
                          <span v-if="quan.couponUseType == 'ORDER'">卡券</span>
                          <span v-if="quan.couponUseType == 'SHOP'">店铺券</span>
                          <strong v-if="quan.couponType == 'DISCOUNT_COUPON'">{{(quan.couponValue/10)}}<small>折</small></strong>
                          <strong v-else><small>￥</small>{{Number(quan.couponValue).toFixed(0)}}</strong>
                        </div>
                        <div class="item-fr">
                          <!-- <span>1张</span> -->
                          <h5>{{quan.activityName}}</h5>
                          <p>{{quan.description}}</p>
                          <p>{{quan.startTime && quan.startTime.split(' ')[0].replace(/-/g,'.')}}—{{quan.startTime && quan.endTime.split(' ')[0].replace(/-/g,'.')}}</p>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div
                    class="dialog-text dialog-gpt"
                    v-show="dialogInfoList[index].dialogStatus==2"
                  >
                    <h4>领取成功！</h4>
                    <p>优惠券已发放到您的账户<br>请到“我的优惠券”查看</p>
                  </div>
                  <div
                    class="dialog-text dialog-pt"
                    v-show="dialogInfoList[index].dialogStatus==3"
                  >
                    <p>确定放弃领取优惠券吗？</p>
                  </div>
                </div>
                <div class="dialog-middle-img"></div>
                <div class="dialog-btns">
                  <a
                    href="javascript:;"
                    class="btn btn-cancel"
                    v-show="dialogInfoList[index].dialogStatus==3"
                    @click="doGiveUp(index)"
                  >确定放弃</a>
                  <a
                    href="javascript:;"
                    class="btn"
                    v-show="dialogInfoList[index].dialogStatus==3"
                    @click="doGetQuan(index)"
                  >立即领取</a>
                  <a
                    href="javascript:;"
                    class="btn-big"
                    v-show="dialogInfoList[index].dialogStatus==1"
                    @click="doGetQuan(index)"
                  >立即领取</a>
                  <a
                    href="javascript:;"
                    class="btn-big"
                    v-show="dialogInfoList[index].dialogStatus==2"
                    @click="goLookQuan(index)"
                  >去查看</a>
                </div>
              </div>
            </div>
          </van-swipe-item>
        </van-swipe>
      </template>
    </div>
  </div>
</template>

<script>
import Api from '../api'

export default {
  name: 'couponActivitysDialog',
  props: {
    source: {
      type: String,
      default: () => '',
    },
    sourceId: {
      type: String,
      default: () => '',
    },
    show: {
      type: Boolean,
      default: () => false,
    },
  },
  data() {
    return {
      dialogStatus: 0, // 弹窗状态  0关闭状态 1立即领取  2领取成功  3是否放弃
      dialogInfo: {}, // 弹窗信息
      dialogInfoList: [], // 弹窗信息列表
      dialogStyle: {}, // 弹框样式
    }
  },
  computed: {
    dialogShow() {
      return this.dialogInfoList && this.dialogInfoList.length > 0
    },
  },
  created() {
    Api.couponActivitysInfo({
      source: this.source,
      sourceId: this.sourceId,
    }).then(res => {
      let result = res.data
      if (result.success && result.data && result.data.length > 0) {
        result.data.forEach(ele => {
          if (ele.coupons && ele.coupons.length) {
            /*
             * targetType(用户身份):1、全部用户 2、新用户 3、老用户
             * (1，2，3)的用户身份在任何场景均需要弹框
             * 其他身份用户需要在(员工、服务兵)：ihaier\爱创客\顺逛\智家 内部弹框
             */
            if ([1, 2, 3].includes(+ele.targetType) || this.$util.isIhaierApp() || this.$util.isIMaker() || this.$util.isSgRn() || this.$util.isUplus() || this.show) {
              ele.dialogStatus = 1
              this.dialogInfoList.push(ele)
            }
          }
        })
      }
    })
  },
  watch: {
    dialogShow(val) {
      if (val === true) {
        this.$nextTick(() => {
          /*
           * 处理在大屏模式下顶部边距超出的问题
           * dialogMaskElementStyle：mask的底部距离
           * dialogElementStyle：dialog的底部距离
           * (childHeight.height + (childHeight.height * 30) / 234)：表示dialog底部内容的高度
           * 如果大屏模式下dialog超出mask的底部，则重新设置dialog的顶部padding：刚好露出底部
           */
          if (this.$refs.dialogMask && this.$refs.dialog && this.$refs.dialog[0]) {
            let dialogMaskElementStyle = this.$refs.dialogMask.getBoundingClientRect() || {}
            let dialogElementStyle = this.$refs.dialog[0].getBoundingClientRect() || {}
            let childHeight = (this.$refs.dialog[0].firstElementChild && this.$refs.dialog[0].firstElementChild.getBoundingClientRect()) || {}
            if (dialogMaskElementStyle.bottom && dialogElementStyle.bottom) {
              if (dialogMaskElementStyle.bottom < dialogElementStyle.bottom) {
                this.dialogStyle = {
                  paddingTop: dialogMaskElementStyle.bottom - (childHeight.height + (childHeight.height * 30) / 234) + 'px',
                }
              }
            }
          }
        })
      }
    },
  },
  methods: {
    touchHandler(e, index) {
      if (this.dialogInfoList[index].coupons.length <= 2) {
        e.preventDefault()
        e.returnValue = false
        return false
      } else {
        e.returnValue = true
        return true
      }
    },
    // 券弹窗
    dialogCloseFn(index) {
      // 2021/08/13 DSG-27833需求，应华要求去掉关闭时确认放弃功能

      // if (this.dialogInfoList[index].dialogStatus === 3 || this.dialogInfoList[index].dialogStatus === 2) {
      this.handleCloseCouponPop({
        activityId: this.dialogInfoList[index].id,
        endTime: this.dialogInfoList[index].endTime
      })
      this.dialogInfoList.splice(index, 1)
      // } else {
      //   this.dialogInfoList[index].dialogStatus = 3
      // }
    },
    // 确认放弃领券
    doGiveUp(index) {
      this.handleCloseCouponPop({
        activityId: this.dialogInfoList[index].id,
        endTime: this.dialogInfoList[index].endTime
      })
      this.dialogInfoList.splice(index, 1)
    },
    /**
     * @description 关闭弹窗优惠券方法
     * @param activityId 所关闭弹窗的活动id
     * @param endTime 优惠券活动结束时间 时间戳
    */
    handleCloseCouponPop(param) {
      return new Promise((resolve, reject) => {
        Api.closeCouponPop({
          activityId: param.activityId,
          endTime: param.endTime
        }).then(res => {
          let result = res.data
          if (result.success) {
            resolve()
          }
        })
      })
    },
    doGetQuan(index) {
      Api.receiveActivitysCoupons({
        activityId: this.dialogInfoList[index].id,
      }).then(res => {
        let result = res.data
        console.log(result, 'doGetQuan result')
        if (result.success) {
          this.dialogInfoList[index].dialogStatus = 2
        }
      })
    },
    goLookQuan(index) {
      this.dialogInfoList.splice(index, 1)
      this.$router.push('/coupon')
    },
  },
}
</script>

