<template>
  <div style="height: 1.02rem">
    <div id="topBar" class="top-bar">
    <!-- <div class="logo"><img :src="info.logo | imageToHttps | imageToWebp($webpSurpport)" alt="" /></div> -->
    <div class="top-search" @click="fnOpenSearch">
      <div>
        <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/home/new/search.png" />
        <div class="text">
          <textScroll :dataList="info.words"/>
        </div>
      </div>
      <!-- <i class="icon-sao" @click.stop="goPage('https://uplus.haier.com/uplusapp/scan/homescanpage.html', false, true)"></i> -->
    </div>
    <div class="top-other">
      <div @click="customerService">
        <img class="service" :src="info.kefu | imageToHttps | imageToWebp($webpSurpport)"/>
      </div>
      <div class="cart" @click="goPage('/testmemoleaks')">
        <img :src="info.cart | imageToHttps | imageToWebp($webpSurpport)" />
        <span v-if="info.cartnum">{{info.cartnum}}</span>
      </div>
      <div @click="showModal"><img :src="info.more | imageToHttps | imageToWebp($webpSurpport)" class="more" /></div>
    </div>
      <div class="search-bg-mask" id="topBarBg" :style="{'background': `${bg}`,'opacity': bgOpacity, 'background-size': `100% ${coverY}` }"></div>
  </div>

  <!-- <lazy-search hiddenId='homeWarp' @go-back="searchComponentShow=false" @search='searchComponentShow=false' v-if='searchComponentLoad' v-show='searchComponentShow' :show='searchComponentShow' defaultRoute/> -->

  <!-- 更多菜单 -->
  <div class="mask" v-show="isShowModal" @click="isShowModal = false" @touchmove.stop.prevent></div>
  <div v-show="isShowModal" @click="isShowModal = false" class="popup-container" :style="{'top': mask}">
    <div class="text" @click="goPage('/messageCenter')"><i class="icon-message"></i>平台消息</div>
    <div class="text" @click="goPage('/orderlist')"><i class="icon-order"></i>我的订单</div>
    <div class="text" @click="goPage('/myCollection')"><i class="icon-fav"></i>我的收藏</div>
    <div class="text" @click="goPage('/coupon')"><i class="icon-quan"></i>优惠券</div>
    <!-- <div class="text" @click="goPage('https://uplus.haier.com/uplusapp/problemFeedBack/feedback.html',true)"><i class="icon-tucao"></i>我要吐槽</div> -->
  </div>
  </div>
</template>

<script>
const textScroll = () => import(/* webpackChunkName: "searchComponent" */'@/components/textscroll/index.vue')

// const lazySearch = () => import(/* webpackChunkName: "searchComponent" */'@/components/search/index.vue')

export default {
  name: 'searchbar',
  components: {
    // lazySearch,
    textScroll,
  },
  props: {
    info: {
      type: Object,
      default: () => {}
    },
    bgType: {
      type: Number,
      default: () => 2
    },
    bg: {
      type: String,
      default: () => ''
    },
    bgOpacity: {
      type: Number,
      default: 1
    },
    mask: {
      type: String,
      default: ''
    }
  },
  // computed: {
  //   textList() {
  //     return this.info.words
  //   },
  // },
  data() {
    return {
      textTimer: null,
      animate: false,
      isShowModal: false, // 更多菜单
      coverY: '',
      searchComponentLoad: false,
      searchComponentShow: false,
      islogining: false, // 是否正在登录中(未拿到token禁止操作)

    }
  },
  watch: {
    bg() {
      /**
       * 下拉刷新 活动背景改变时 强制更新
       */
      this.$nextTick(() => {
        if (this.bgType === 5) { // 上下渐变
          this.coverY = '2.48rem'
          document.getElementById('topBarBg') && (document.getElementById('topBarBg').style = `padding-top: ${this.top}; opacity: ${this.bgOpacity}; background: ${this.bg}; background-size: 100% ${this.coverY};`)
        } else {
          document.getElementById('topBarBg') && (document.getElementById('topBarBg').style = `padding-top: ${this.top}; opacity: ${this.bgOpacity}; background: ${this.bg}; background-size: 100% auto;`)
        }
      })
    }
  },
  mounted() {
    // this.textStartMove()
  },
  methods: {
    // textStartMove() {
    //   clearInterval(this.textTimer)
    //   this.textTimer = setInterval(this.showMarquee, 2000);
    //   document.getElementById('textSearch').addEventListener('webkitTransitionEnd', () => {
    //     this.textList.push(this.textList[0]);
    //     this.textList.shift();
    //     this.animate = false;
    //   })
    // },
    // showMarquee() {
    //   if (this.textList.length > 1) {
    //     this.animate = true;
    //   }
    // },
    goPage(path, intact) {
      switch (path) {
        case '/messageCenter':
          this.$util.gioTrackWithVariable('mall_index_click_platformMsg')
          break;
        case '/orderlist':
          this.$util.gioTrackWithVariable('mall_index_click_myOrders')
          break;
        case '/myCollection':
          this.$util.gioTrackWithVariable('mall_index_click_myCollections')
          break;
        case '/coupon':
          this.$util.gioTrackWithVariable('MB15591')
          break;
        case '/cart':
          this.$util.gioTrackWithVariable('mall_index_click_cart')
          break;
      }
      if (intact) {
        location.href = path
      } else {
        this.$router.push(path)
      }
    },
    /**
    * @description 客服
    */
    customerService() {
      this.$util.gioTrackWithVariable('mall_index_click_IM')
      if (this.islogining) return
      if (!this.$store.getters.getLoginStatus) {
        this.islogining = true
        this.$store.dispatch('doLogin').then(res => {
          if (res !== 400) {
            this.islogining = false
          } else {
            this.islogining = false
          }
        }).catch(() => {
          this.islogining = false
        })
      } else {
        // 如果是海尔app隐藏客服头部栏，使用app内头部栏，否则使用客服工具头部栏
        this.$store.dispatch('goPageByType', {
          type: 29,
          params: {
            sceneId: 'cj090'
          }
        })
      }
    },
    showModal () {
      this.isShowModal = !this.isShowModal;
    },
    /**
    * @description 点击搜索
    */
    fnOpenSearch() {
      this.$util.gioTrackWithVariable('mall_index_click_searchBox')
      this.$router.push({
        path: '/goodsSearch',
      })
      // if (!this.searchComponentLoad) {
      //   lazySearch().then(() => {
      //     this.searchComponentLoad = true;
      //     this.searchComponentShow = true;
      //   })
      //   return;
      // }
      // this.searchComponentShow = true;
    },
  }
}
</script>

