<template>
  <div class="category" v-if="items.length">
    <div class="category-item" :class="{'category-top': index < 5}" v-for="(category, index) in items" :key="category.id" @click="goPage(category, index)">
      <vanImg class="catetory-img" :src="category.iconUrl | imageToHttps | imageToWebp($webpSurpport)" alt=""/>
      <span :style="{'color': category.fontColor}">{{category.name}}</span>
    </div>
    <div class="category-mask" :style="{'background': bgColor,'opacity': bgOpacity, 'background-size': '100% 100%'}"></div>
  </div>
</template>

<script>

export default {
  name: 'categoryItems',
  props: {
    items: {
      type: Array,
      default: () => []
    },
    bgColor: {
      type: String,
      default: '#fff'
    },
    bgOpacity: {
      type: Number,
      default: 1
    }
  },
  methods: {
    goPage(item, index) {
      console.log(item)
      // 埋点
      this.$util.gioTrackWithVariable('MB15593', {
        site_number: index + 1 + '',
        text: item.name || 'null'
      })
      if (item.hyperType === undefined) {
        this.$store.dispatch('bannerTypeFiveClick', { hyperLink: item.linkUrl });
      } else {
        this.$store.dispatch('goPageByType', {
          type: item.hyperType,
          params: item.hyperParam
        })
      }

      // if (item.linkUrl.includes('.ehaier.com/sgmobile')) {
      //   this.$router.push(item.linkUrl.split('.ehaier.com/sgmobile')[1])
      // } else {
      //   location.href = item.linkUrl
      // }
      // this.$router.push(item.linkUrl, false, true)
    }
  }
}
</script>

