<template>
  <div class="king-kong">
    <div class="king-warp">
      <div class="king-items" v-scroll="scrollFn">
        <div class="king-item" v-for="(item, index) in items" :key="item.id" @click="goPage(item, index)">
          <vanImg class="king-img" :src="item.iconUrl | imageToHttps | imageToWebp($webpSurpport)" alt="" />
          <span :style="{'color': `${item.fontColor}`}">{{item.name}}</span>
        </div>
      </div>
    </div>
    <div id="kpw" class="king-process" v-if="items.length > 5"><span id="kp" :style="{left: processLeft}"></span></div>
    <div class="king-kong-mask" :style="{'background': bgColor,'opacity': bgOpacity}"></div>
  </div>
</template>

<script>

export default {
  name: 'kingkong',
  props: {
    items: {
      type: Array,
      default: () => []
    },
    bgColor: {
      type: String,
      default: '#fff'
    },
    bgOpacity: {
      type: Number,
      default: 1
    }
  },
  data() {
    return {
      processLeft: 0,
      moveLeft: ''
    }
  },
  mounted() {
    let kpw = document.getElementById('kpw').offsetWidth
    let kp = document.getElementById('kp').offsetWidth
    this.moveLeft = (kpw - kp) / kpw
  },
  directives: {
    scroll: {
      bind(el, binding) {
        el.addEventListener('scroll', (ev) => {
          let items = document.querySelectorAll('.king-item')
          let itemsWidth = 0;
          items.forEach(item => {
            itemsWidth += item.offsetWidth
          })
          // console.log(ev.target.scrollLeft, ev.target.offsetWidth)
          binding.value(ev.target.scrollLeft / (itemsWidth - ev.target.offsetWidth))
        })
      }
    }
  },
  methods: {
    scrollFn(left) {
      this.processLeft = `${(this.moveLeft * left * 100)}%`;
    },
    goPage(item, index) {
      // 埋点
      this.$util.gioTrackWithVariable('MB15595', {
        site_number: index + 1 + '',
        text: item.name || 'null'
      })
      if (item.hyperType === undefined) {
        this.$store.dispatch('bannerTypeFiveClick', { hyperLink: item.linkUrl });
      } else {
        this.$store.dispatch('goPageByType', {
          type: item.hyperType,
          params: item.hyperParam,
        })
      }

      // if (item.linkUrl.includes('.ehaier.com/sgmobile')) {
      //   // this.$router.push(item.linkUrl.split('.ehaier.com/sgmobile')[1])
      //   this.$store.dispatch('bannerTypeFiveClick', { hyperLink: item.linkUrl });
      // } else {
      //   location.href = item.linkUrl
      // }
      // this.$router.push(item.linkUrl, false, true)
    }
  }
}
</script>

