<template>
  <div class="store-page" @scroll="scrollFn">
    <Header></Header>
    <div class="store-list" v-if="storeList.length">
      <div class="store-item" v-for="item in storeList" :key="item.storeId">
        <div class="store-title">
          <h4 @click="look(item)"><p>{{item.storeAliasName}}</p> <span>官方认证</span></h4>
          <div @click="look(item)">进店 <i></i></div>
        </div>
        <div class="store-tag">
          <span>商品描述 <small>{{item.productScore || 0}}</small></span>
          <span>卖家服务 <small>{{item.sellerServiceScore || 0}}</small></span>
          <span>物流服务 <small>{{item.logisticsScore || 0}}</small></span>
        </div>
        <div class="store-body">
          <div v-for="prod in item.membersImages" :key="prod.productId" @click="goDetail(prod.productId)"><img :src="prod.defaultImageUrl" alt=""></div>
        </div>
      </div>
    </div>
    <div v-else-if="loadEnd && !storeList.length" class="empty-store">
      <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/order/nodata.png" alt="">
      <p>暂无店铺</p>
    </div>
    <div class="loading" id="loading">
      <p v-if="loading && !loadEnd">加载中...</p>
      <p v-else>到底了~</p>
    </div>
  </div>
</template>

<script>
import Header from '@/components/header'
import Url from '@/http/url'
import { mapGetters } from 'vuex'

import { commonLoadItemNew } from '@/http/service/shop'

export default {
  name: 'vipstore',
  components: {
    Header
  },
  data() {
    return {
      // shareShow: this.$util.isHaierApp() || this.$util.isSgRn(),
      storeList: [],
      loading: false,
      loadEnd: false,
      curPage: 1,
      pageSize: 10,
    }
  },
  computed: {
    ...mapGetters({
      location: 'getlocationInfo',
    })
  },
  mounted() {
    if (this.$util.isHaierApp()) {
      this.$store.dispatch('handleUpAuth').then(isLogin => {
        // 登陆正常 调用加购接口
        if (isLogin) {
          this.fetchData()
        } else {
          this.$store.dispatch('doLogin')
        }
      })
    } else {
      if (this.$store.getters.getLoginStatus) {
        this.fetchData()
      }
    }
  },
  methods: {
    scrollFn(ev) {
      let scrollTop = ev.target.scrollTop
      let loadingTop = document.getElementById('loading').getBoundingClientRect().top;
      let winH = window.innerHeight;
      console.log(scrollTop, loadingTop, winH)
      if (scrollTop > loadingTop - winH) {
        this.fetchData()
      }
    },
    fetchData() {
      console.log('fetch')
      if (this.loading || this.loadEnd) return;
      this.loading = true
      this.$http.get(Url.vipStore, {
        params: {
          latitude: this.location.latitude,
          longitude: this.location.longitude,
          pageIndex: this.curPage,
          pageSize: this.pageSize
        }
      }).then(res => {
        let result = res.data;
        this.loading = false
        if (result.success) {
          let arr = this.getItemProductImg(result.data || []) // 循环请求获取每项数据
          this.storeList = this.storeList.concat(arr)
          this.curPage += 1;
          if (result.data.length < this.pageSize) {
            this.loadEnd = true
          }
        } else {
          this.$toast(result.message);
        }
      })
    },
    goDetail(productId) {
      this.$router.push(`/goodsDetail?productId=${productId}`)
    },
    look(item) {
      this.$router.push(`/shopDetail?storeId=${item.storeId}`)
    },

    getItemProductImg(arr) {
      arr.forEach(item => {
        commonLoadItemNew({
          channal: 1,
          provinceId: this.location.provinceId,
          cityId: this.location.cityId,
          districtId: this.location.districtId,
          streetId: this.location.townshipId,
          visitStore: item.storeId,
          filterData: 'hasStock@all@0@',
          qs: 'saleDesc',
          fromType: 2,
          pageIndex: 1,
          pageSize: 10,
        }).then(res => {
          let result = res.data;
          console.log(result, '======result item=> ' + item.storeId + '========')
          item.membersImages = result.productList.splice(0, 3);
        })
      })
      return arr
    }

  }
}
</script>

