<template>
  <div class="coupon-warp" v-if="usableItems&&usableItems.length">
    <div class="coupon-items">
      <div class="coupon-item" v-for="coupon in usableItems" :key="coupon.activityCode+coupon.couponTemplateCode" @click="doGetCoupon(coupon)">
        <div class="coupon-info">
          <!-- <h4><span>￥</span>{{coupon.couponValue}}</h4> -->
          <coupon-value-label :forHomePage="true" :couponValue='coupon.couponValue' :couponType='coupon.couponType'></coupon-value-label>
          <h5>{{coupon.description}}</h5>
          <p>{{coupon.couponTemplateName}}</p>
        </div>
        <div class="coupon-btn">{{coupon.displayType == 2 ? '去 使 用' : '领 取'}}</div>
        <div class="coupon-status" v-if="coupon.displayType == 2"></div>
      </div>
      <div class="coupon-more" @click="more()">
        <i></i>
        <p>查看更多</p>
      </div>
    </div>
  </div>
</template>

<script>
import Api from '../api';
import couponValueLabel from '@/components/couponValueLabel'

export default {
  name: 'couponItems',
  components: { couponValueLabel },
  props: {
    items: {
      type: Array,
      default: () => []
    }
  },
  computed: {
    usableItems() {
      return this.items.filter(item => item.displayType !== '5' && item.displayType !== 5)
    }
  },
  methods: {
    doGetCoupon(item) {
      if (Number(item.displayType) === 2) {
        this.$router.push(`/usablecouponlist?couponTemplateCode=${item.couponTemplateCode}&activityCode=${item.activityCode}&couponType=${item.couponType}&sourceChannel=${item.sourceChannel}`)
      } else {
        if (this.$store.state.isLogin) {
          const streetId = this.$store.getters['getlocationInfo'].townshipId;
          Api.getCouponItem({
            activityCode: item.activityCode,
            couponTemplateCode: item.couponTemplateCode,
            sourceChannel: item.sourceChannel,
            streetId
            // couponFrom: item.couponFrom || '',
          }).then(res => {
            let result = res.data
            console.log(result)
            if (result.success && result.data.couponCode) {
              item.displayType = 2
            } else {
              this.$toast(result.message)
            }
          })
        } else {
          // this.$router.push('/login')
          this.$store.dispatch('doLogin');
        }
      }
    },
    more() {
      this.$router.push(`/voucher`)
    }
  }
}
</script>

