<template>
  <div id="btmWarp" class="btm-warp-list">
    <div class="btm-catgory-tab" :class="{'active-fixed': isFixed}" :style="{top: fixedTop}">
      <div class="btm-tab-warp">
        <div v-for="item in btmCategoryNav" :key="item.productCateId" :class="{'active': curproductCateId == item.productCateId}" @click="changeTab(item,$event)">
          <h6>{{item.name}}</h6><span>{{item.tag}}</span>
        </div>
      </div>
    </div>
    <div v-if="isFixed" :style="{height: '.9rem'}"></div>
    <div class="btm-category-warp" v-if="isOnline">
      <div class="category-empty" v-if="!productCateList.length && fetchEnd">
        <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/home/new/empty.png" alt="">
      </div>
      <div id="btmCateList" class="btm-category-list" v-else>
        <lazy-public-product-list id='pop-up-hidden' :productList="productCateList" :switchStyle='true' />
        <!-- <div class="btm-list-item" v-for="item in productCateList" :key="item.productId" @click="toProduct(item)">
          <div class="btm-item-img">
            <vanImg :src="item.defaultImageUrl | imageToHttps"/>
          </div>
          <div class="btm-item-name"><span v-if="item.recommend">推荐</span>{{item.productFullName}}</div>
          <div class="btm-item-sellpoint">
            <span v-for="(point,indx) in item.sellPoint" :key="indx">{{point}}</span>
          </div>
          <div class="btm-item-footer">
            <p><span>￥</span>{{item.finalPrice | formatPrice}}</p>
            <strong :class="{'disabled': item.hasStock === '无货'}">{{item.hasStock}}</strong>
          </div>
          <div class="btm-item-commit">
            <span>{{item.comments == '暂无评价'?item.comments:item.comments+'条评价'}}</span>
            <span v-if="item.comments !== '暂无评价'">{{item.goodRate}}好评</span>
          </div>
        </div> -->
      </div>
      <div class="loading-text" v-scroll="scrollFn">
        <div v-if="!fetchEnd">加载中...</div>
        <div v-else>没有更多了</div>
      </div>
    </div>
    <div class="btm-category-warp" v-else>
      <div class="category-empty">
        <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/home/new/empty.png" alt="">
        <div class="loading-text">没有更多了</div>
      </div>
    </div>
  </div>
</template>

<script>
import Api from '../api';
const lazyPublicProductList = () => import(/* webpackChunkName: "productList" */'@/components/publicProductList/index')

let scrollBindFn = null

export default {
  name: 'btmProducts',
  components: {
    lazyPublicProductList
  },
  props: {
    location: {
      type: Object,
      default: () => {}
    },
    isOnline: {
      type: Boolean,
      default: () => true
    }
  },
  directives: {
    scroll: {
      bind: function(el, binding) {
        scrollBindFn = (ev) => {
          let fixedTop = document.getElementById('btmWarp') && document.getElementById('btmWarp').offsetTop
          let topBarHeight = document.getElementById('btmWarp') && document.getElementById('topBar').offsetHeight
          if (el.getBoundingClientRect().top < window.innerHeight + 200) {
            binding.value('fetch')
          }
          if (ev.target.scrollTop >= fixedTop) {
            binding.value('fixed', topBarHeight)
          } else {
            binding.value('fixed', 0)
          }
        }
        document.getElementById('app-warp').addEventListener('scroll', scrollBindFn)
      }
    }
  },
  data() {
    return {
      isFixed: false,
      fixedTop: '',
      curproductCateId: 2729,
      btmCategoryNav: [
        {
          productCateId: 2729,
          name: '空调',
          tag: '悦享呼吸',
        }, {
          productCateId: 2725,
          name: '洗衣机',
          tag: '守护洁净',
        }, {
          productCateId: 2723,
          name: '冰箱',
          tag: '原味鲜活',
        }, {
          productCateId: 2743,
          name: '彩电',
          tag: '视听万象',
        }, {
          productCateId: 2741,
          name: '热水器',
          tag: '温暖到家',
        }, {
          productCateId: 2742,
          name: '厨房电器',
          tag: '乐享美味',
        }, {
          productCateId: 2726,
          name: '冷柜',
          tag: '冻力强劲',
        }, {
          productCateId: 2973,
          name: '智能产品',
          tag: '科技体验',
        }, {
          productCateId: 4255,
          name: '生活电器',
          tag: '品质生活',
        }, {
          productCateId: 2774,
          name: '水家电',
          tag: '洁净用水',
        }, {
          productCateId: 2811,
          name: '家用中央空调',
          tag: '舒适陪伴',
        }
      ],
      productCateList: [],
      fetching: false,
      fetchEnd: false,
      curpage: 1,
      pageSize: 10,
      IntersectionObserver: null
    }
  },
  mounted() {
    this.fetchData()
  },
  activated() {
    document.getElementById('app-warp').addEventListener('scroll', scrollBindFn)
  },
  deactivated() {
    document.getElementById('app-warp').removeEventListener('scroll', scrollBindFn)
  },
  watch: {
    isOnline(newVal) {
      if (newVal) {
        this.fetchData()
      }
    }
  },
  methods: {
    scrollFn(status, position) {
      if (status === 'fetch') {
        this.fetchData()
      }
      if (status === 'fixed') {
        if (position) {
          this.isFixed = true
        } else {
          this.isFixed = false
        }
        this.fixedTop = position + 'px'
      }
    },
    fetchData(flag) {
      if (this.fetching || this.fetchEnd) return
      this.fetching = true;
      Api.getCommonLoadItemNew({
        pholder: 1,
        productCateId: this.curproductCateId,
        provinceId: this.location.provinceId,
        cityId: this.location.cityId,
        districtId: this.location.districtId,
        streetId: this.location.townshipId,
        fromType: 1,
        memberId: '',
        qs: 'isHotDesc',
        filterData: '',
        pageIndex: this.curpage,
        pageSize: this.pageSize,
      }).then(res => {
        let result = res.data;
        if (result.success) {
          let productList = result.data.productList
          let traceId = result.data.traceId
          if (productList.length && traceId) {
            Api.getPriceByProduct({ traceId }).then(({ data }) => {
              if (data.success) {
                let prodsArr = data.products
                productList.forEach(item => {
                  prodsArr.forEach(prod => {
                    if (prod.sku === item.sku) {
                      item.finalPrice = prod.lowestFlashPrice || prod.finalPrice
                      item.isLowestFlash = !!prod.lowestFlashPrice
                      item.skku = prod.skku
                      item.commission = prod.commission
                      item.priceType = prod.priceType
                      item.defaultPrice = prod.defaultPrice
                      item.promotion = prod.promotion
                      item.productActNew = prod.productActNew
                      item.productActTerm = prod.productActTerm
                      item.plusPrice = prod.plusPrice
                      item.clustersNum = prod.clustersNum
                    }
                  })
                })
                if (flag) this.productCateList = []
                this.productCateList = this.productCateList.concat(productList)
                this.productCateList.forEach((item, index) => {
                  item.listIndex = index
                  item.productCateId = this.curproductCateId
                })
                this.fetching = false;
                this.curpage += 1;

                if (productList.length < this.pageSize) {
                  this.fetchEnd = true
                }
                window.setTimeout(() => {
                  this.createIntersectionObserver()
                }, 500)
              }
            })
          } else {
            if (flag) this.productCateList = []
            this.fetchEnd = true
          }
        }
      }).catch(() => {
        this.fetchEnd = true
      })
    },
    createIntersectionObserver () {
      const options = {
        root: document.querySelector('#app'),
        rootMargin: '50px 0px',
        threshold: [0.01]
      }
      if (this.IntersectionObserver) {
        this.IntersectionObserver.disconnect()
      }
      this.IntersectionObserver = new IntersectionObserver((entries) => {
        for (const ent of entries) {
          if (ent.isIntersecting) {
            this.$util.gioTrackWithVariable('MB13405', {
              // user_id: localStorage.getItem('ucId') ? localStorage.getItem('ucId') + '' : 'null',
              // member_id: localStorage.getItem('mId') ? localStorage.getItem('mId') + '' : 'null',
              roughly_classification_id: this.curproductCateId ? this.curproductCateId + '' : 'null',
              productId_var: ent.target.dataset.id + '',
              site_number: ~~ent.target.dataset.index + 1 + ''
            })
          }
        }
      }, options)
      for (const dom of document.querySelectorAll('.public-double-row-goods')) {
        this.IntersectionObserver.observe(dom)
      }
    },

    changeTab(item, ev) {
      // 埋点
      this.$util.gioTrackWithVariable('MB15599', {
        category_name: item.name || 'null'
      })
      this.curproductCateId = item.productCateId
      this.fetching = false
      this.fetchEnd = false
      this.curpage = 1
      this.fetchData(true)
      ev.target.scrollIntoView({ behavior: 'smooth', inline: 'center', block: 'nearest' })
      // document.getElementById('btmWarp').scrollIntoView({ behavior: 'smooth', block: 'start' })
      let positon = document.getElementById('btmCateList').offsetTop
      document.getElementById('app-warp').scroll(0, positon - 40)
    },

    toProduct(prod) {
      this.$router.push(`/goodsDetail?productId=${prod.productId}`)
    }
  }
}
</script>

